import { lightTheme, darkTheme } from 'styles/Theme';
import React, { useEffect, useState } from 'react';
import { useAppSelector } from 'state/hooks/hooks';
import { BrowserRouter, Switch, Route, Redirect } from 'react-router-dom';
import MainTemplate from 'components/templates/MainTemplate/MainTemplate';
import MainPage, { Post } from './MainPage/MainPage';
import { DefaultTheme } from 'styled-components';
import FormPage from './FormPage/FormPage';
import ArticlePage from './ArticlePage/AriclePage';

const Root: React.FC = () => {
  const isDark: boolean = useAppSelector((state) => state.changeTheme.dark);
  let Theme: DefaultTheme = isDark ? darkTheme : lightTheme;
  const [posts, setPosts] = useState([]);
  useEffect(() => {
    var Local = localStorage.getItem('posts');
    console.log(localStorage.getItem('posts'));
    console.log(Local);
    if (Local !== null) {
      setPosts(JSON.parse(Local));
      console.log(JSON.parse(Local));
    }
  }, []);

  return (
    <BrowserRouter>
      <MainTemplate Theme={Theme}>
        <Switch>
          <Route exact path="/blogFrontendApp" component={MainPage} />
          <Route
            path="/blogFrontendApp/MainPage"
            render={() => <Redirect to="/blogFrontendApp" />}
          />
          <Route path="/blogFrontendApp/AddArticle" component={FormPage} />
          <Route
            path="/blogFrontendApp/addarticle"
            render={() => <Redirect to="/blogFrontendApp/AddArticle" />}
          />
          {posts.map((post: Post) => {
            const Path = '/blogFrontendApp/posts' + post.id;
            console.log(Path);
            return (
              <Route
                exact
                path={Path}
                render={() => <ArticlePage post={post} />}
                key={post.id}
              />
            );
          })}
        </Switch>
      </MainTemplate>
    </BrowserRouter>
  );
};

export default Root;
